.card {
    background: #fff;
    border-radius: 2px;
    border: 5px solid #f1f1f1;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 200px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    float: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:white;
    margin: auto;
  }
  .card > .img-container > img {
    width: 80%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .card > .remove {
    color: #ff1744;
    font-size: 48px;
    position: absolute;
    top: -20px;
    right: 5px;
    cursor: pointer;
  }
  
  .card > .remove:hover {
    color: #d50000;
  }
  


